.hero {
  position: relative ;
   background-color:$lite-pink-bg-color ;
   @include for-phone-up{ height:502px;}
   @include for-tablet-up{ height:416px;}
   @include for-desktop{ height:664px };
 
   &__title{
      z-index: 99;
      font-size: 26px;
      line-height: 1.07;
      font-weight: 400;
      font-family: 'Titan One';
      color:$main-white-color;
      margin-bottom: 20px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      @include for-tablet-up{
         font-size:22px ;
      }
      @include for-desktop{
         font-size:38px ;
      }
   }
      &__title-passion{
         font-size: 48px;
         color:$titan-title-big-color ;
         @include for-tablet-up {
               font-size:34px ;
            }
         
            @include for-desktop {
               font-size: 58px;
            }

      }
}

.product-button {
   margin-right: 4px;
   border-radius: 20px;
   border: none;
   cursor: pointer;
   padding: 8px 16px;
   font-family: 'DM Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 8px;
      line-height: 1.25;
      color: $main-white-color;
      background-color:$titan-title-big-color ;
       @include  for-desktop{
      font-size: 14px;
   line-height: 1.28;
   padding: 11px 23px;
   
}
}
.made-button {
   font-family: 'DM Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 8px;
      line-height: 1.25;
      padding: 8px 16px;
      border-radius: 20px;
      border: none;
      background-color:$main-white-color ;
      color: $titan-title-big-color;
      @include for-desktop {
            font-size: 14px;
            line-height: 1.28;
            padding: 11px 23px;
         }
}
.wrap-hero{
  
   @include for-tablet-up{
      margin-top: 0;
   }
   @include for-desktop {
         margin-top: 40px;
       }
  margin-top: 136px;
  display: flex;
  flex-direction: column;
  &__buttons{
   z-index: 99;
  }
 
}

.mobile-ice-cream {
   @include for-tablet-up-min{
display: none;
   }
position: absolute;
z-index: 9;
top:18.5%;
left: 35%;

}
.mobile-ellipse{
   @include for-tablet-up-min {
display: none;
   }
   position: absolute;
   top: 14%;
   left: 16%;
 
}